@import 'partials/variables';
@import 'partials/mixins';

#mci-meta
{
	*
	{
		user-select: none;
	}

	a,
	a:hover,
	a:active,
	a:focus
	{
		outline:    none;
		box-shadow: none;
	}

	.notification
	{
		display:      none;
		color:        #0085BA;
		padding-left: $gs;
	}

	.id, .dummy
	{
		display: none;
	}

	.mci
	{
		//background:    #F7F7F7;
		background:    #F1F1F1;
		//border:        1px solid #ddd;
		margin-top: $gs;
		border-radius: 8px;
		overflow:      hidden;

		&:nth-of-type(2)
		{
			margin-top: $gs;
		}

&:last-of-type{
	margin-bottom: $gs/2;
}

		.mci-header
		{
			@include clear-after();
			position: relative;
			cursor:   move;

			.mci-icon-fancybox,
			.mci-icon
			{
				display: block;
				height:  $icon-size;
				width:   $icon-size;
				//border-right: 1px solid #C7C7C7;
			}

			.mci-icon
			{
				background-repeat:   no-repeat;
				background-position: center center;
				background-size:     cover;
			}

			.mci-toggle
			{
				background-image:    url('../img/down.svg');
				background-size:     35px;
				background-position: center center;
				background-repeat:   no-repeat;
				//width:               $icon-size - 2 * $gs;
				//height:              $icon-size - 2 * $gs;
				position:            absolute;
				//top:                 $gs;
				//right:               3 * $gs;
				cursor:              pointer;

				height:              100%;
				width:               $icon-size;
				top:                 0;
				right:               0;

				&:hover
				{
					background-color: #E7E7E7;
				}

				&.toggled
				{
					transform: rotate(-180deg);
				}
			}

			.mci-control
			{
				position:            absolute;
				//right:             0;
				//width:             32px;
				//height:            32px;
				cursor:              pointer;
				background-repeat:   no-repeat;

				background-position: center;
				border-right:        1px solid #DDDDDD;
				height:              100%;
				width:               $icon-size;
				top:                 0;

				&.disabled
				{
					background: none;
					cursor:     default;

					&:hover
					{
						background-color: #F1F1F1;
					}
				}

				&:hover, &:active
				{
					background-color: #E7E7E7;
				}
			}

			.mci-upload-label
			{
				background-image: url('../img/upload.svg');
				//background-position: 7px 6px;
				background-size:  27px;
				right:            3 * $icon-size + 2px;

				&:hover
				{
					//padding-bottom: 0;
					//border-bottom:  1px solid #C7C7C7;
				}

				.mci-upload
				{
					display: none;
				}
			}

			.mci-recrop
			{
				//top:                 32px;
				//border-top:          1px solid #C7C7C7;
				//border-bottom:       1px solid #C7C7C7;
				background-image: url('../img/crop.svg');
				//background-position: 7px 6px;
				background-size:  38px;
				//z-index:             9;
				right:            2 * $icon-size + 1px;
			}

			.mci-remove
			{
				//bottom:              0;
				background-image: url('../img/delete.svg');
				//background-position: 8px 8px;
				background-size:  32px;
				right:            $icon-size;
			}

			.mci-icon-fancybox,
			.mci-header-info,
			.mci-icon
			{
				float: left;
			}

			.mci-header-info
			{
				padding: $gs;

				> div
				{
					font-size:      14px;
					line-height:    24px;
					font-weight:    300;
					letter-spacing: 0.4px;
				}

				.info-title
				{
					font-size:      16px;
					letter-spacing: 0.6px;
					font-weight:    400;
					line-height:    28px;
				}

				.info-shortcode,
				.info-id
				{
					opacity: 0.8;
				}

				.info-shortcode
				{
					cursor: pointer;

					input
					{
						position: absolute;
						top:      -9999px;
					}

					&:hover
					{
						opacity: 1;
					}
				}
			}
		}

		.mci-body
		{
			display:    none;
			padding:    $gs;
			//box-shadow: inset 0 10px 10px -10px grey;
			border-top: 1px solid #DDDDDD;

			.mci-controls-container
			{
				padding: 0 0 $gs 0;
				display: none;

				.mci-size-label
				{
					padding:        10px 12px 10px 0;
					height:         40px;
					line-height:    1;
					font-size:      16px;
					color:          #444444;
					letter-spacing: 1px;
					font-weight:    300;
				}

				select
				{
					-webkit-appearance:  none;
					background-position: right 12px center;
					background-repeat:   no-repeat;
					background-image:    url('../img/select_arrow.svg');
					background-size:     12px;

					padding:             10px 32px 10px 12px;
					background-color:    #FFFFFF;
					box-shadow:          none;
					height:              40px;
					line-height:         20px;
					line-height:         1;
					font-size:           16px;
					color:               #444444;
					letter-spacing:      1px;
					font-weight:         300;
					border-radius:       8px;

					//border:              1px solid #DDDDDD;
					border:              none;
				}

				.cancel
				{
					float:            right;
					margin-left:      $gs;

					padding:          10px 12px;
					background-color: #FFFFFF;
					box-shadow:       none;
					height:           40px;
					line-height:      20px;
					font-size:        16px;
					color:            #444444;
					letter-spacing:   1px;
					font-weight:      300;
					border-radius:    8px;

					//border:           1px solid #DDDDDD;
					border:           none;

					&:active
					{
						transform: none;
					}
				}
			}

			.mci-image-container
			{
				line-height: 0;
				//height: 0;
			}

			.mci-meta-container
			{
				@include clear-after();

				div
				{
					box-sizing: border-box;
					float:      left;
					width:      50%;

					&:first-of-type
					{
						padding-right: $gs / 2;
					}

					&:last-of-type
					{
						padding-left: $gs / 2;
					}
				}

				input
				{
					margin: 0 0 $gs 0;
				}

				input,
				textarea
				{
					width:         100%;
					display:       block;
					box-shadow:    none;
					//border:     1px solid #DDD;
					border:        none;
					border-radius: 8px;
					color:         #000000;

					&::-webkit-input-placeholder, &::placeholder
					{
						opacity:     0.8;
						font-weight: 300;
						font-size:   16px;
					}
				}

				input
				{
					padding: 0 12px;
					height:  40px;
				}

				textarea
				{
					padding:   12px 12px;
					resize:    none;
					font-size: 16px;
				}
			}
		}

		.cropper-container
		{
			margin-bottom: $gs;
		}
	}

	.mci-add
	{
		background-color: #F1F1F1;
		box-shadow:       none;
		height:           $icon-size;
		line-height:      $icon-size;
		font-size:        24px;
		color:            #555555;
		letter-spacing:   2px;
		font-weight:      100;
		border-radius:    8px;
		width:            100%;
		padding:          0;
		text-align:       center;
		margin-top: $gs/2;

		//border:           1px solid #DDDDDD;
		border:           none;

		&:hover
		{
			background-color: #E7E7E7;
		}

		&:active
		{
			transform: none;
		}
	}
}

@media(max-width: 1100px)
{
	#mci-meta
	{
		.mci
		{

			.mci-header
			{
				.mci-icon-fancybox,
				.mci-icon
				{
					height: $icon-size-m;
					width:  $icon-size-m;
				}

				.mci-toggle
				{
					background-size:   20px;
					background-repeat: no-repeat;
					height:            100%;
					width:             50px;
					right:             53px;
				}

				.mci-control
				{
					position:            absolute;
					//right:             0;
					//width:             32px;
					//height:            32px;
					cursor:              pointer;
					background-repeat:   no-repeat;

					background-position: center;
					border-right:        0;
					border-left:         1px solid #C7C7C7;
					height:              100%;
					width:               $icon-size-m;
					top:                 0;
				}

				.mci-upload-label
				{
					//background-position: 7px 6px;
					background-size: 16px;
					right:           0;
					height:          26px;
					width:           52px;
				}

				.mci-recrop
				{
					top:             26px;
					border-top:      1px solid #C7C7C7;
					border-bottom:   1px solid #C7C7C7;
					//background-position: 7px 6px;
					background-size: 16px;
					//z-index:             9;
					right:           0;
					height:          26px;
					width:           52px;
				}

				.mci-remove
				{
					top:             auto;
					bottom:          0;
					//background-position: 8px 8px;
					background-size: 16px;
					right:           0;
					height:          26px;
					width:           52px;
				}

				.mci-header-info
				{
					padding: 8px $gs;

					> div
					{
						font-size:      14px;
						line-height:    20px;
						font-weight:    300;
						letter-spacing: 0.4px;
					}

					.info-title
					{
						font-size:      14px;
						letter-spacing: 0.6px;
						line-height:    24px;
					}

					.info-shortcode,
					.info-id
					{
						opacity: 0.8;
					}

					.info-shortcode
					{
						cursor: pointer;

						input
						{
							position: absolute;
							top:      -9999px;
						}

						&:hover
						{
							opacity: 1;
						}
					}
				}
			}

			.mci-body
			{
				.mci-controls-container
				{
					padding: 0 0 $gs 0;
					display: none;

					.mci-size-label
					{
						padding:        10px 0 10px 0;
						height:         40px;
						line-height:    1;
						font-size:      14px;
						color:          #444444;
						letter-spacing: 1px;
						font-weight:    300;
					}

					select
					{
						background-position: right 10px center;
						background-image:    url('../img/select_arrow.svg');
						background-size:     10px;

						padding:             8px 22px 8px 8px;
						line-height:         20px;
						line-height:         1;
						font-size:           14px;
						letter-spacing:      0;
						border-radius:       8px;

						//border:              1px solid #DDDDDD;
						border:              none;
					}

					.cancel
					{
						margin-left:    0;

						padding:        8px 8px;
						box-shadow:     none;
						height:         40px;
						line-height:    20px;
						font-size:      14px;
						letter-spacing: 0;

						//border:           1px solid #DDDDDD;
						border:         none;

						&:active
						{
						}
					}
				}

				.mci-meta-container
				{
					@include clear-after();

					div
					{
						float: none;
						width: 100%;

						&:first-of-type
						{
							padding-right: 0;
						}

						&:last-of-type
						{
							padding-left: 0;
						}
					}

					input
					{
						margin: 0 0 $gs 0;
					}

					textarea
					{

						&:first-of-type
						{
							margin: 0 0 $gs 0;
						}
					}
				}
			}
		}

		.mci-add
		{
			height:      $icon-size-m;
			line-height: $icon-size-m;
		}
	}
}